import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import * as Extensions from "trimble-connect-project-workspace-api";

const root = ReactDOM.createRoot(document.getElementById('root'));

this.API = await Extensions.connect(
  window.parent,
  (event, args) => {
    switch (event) {
      case "extension.command":
        alert("extension.command")
        //"Command executed by the user: args.data"
        break;
      case "extension.accessToken":
        alert("extension.accessToken")
        //"Accestoken or status: args.data"
        break;
      case "extension.userSettingsChanged":
        alert("extension.userSettingsChanged")
        //"User settings changed!"
        break;
      default:
        alert("a passe")
    }
  },
  30000
);

